.ProgressBars {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 25px 0; }

.ProgressBar-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%; }
  .ProgressBar-container:not(:last-child) {
    margin-bottom: 20px; }

.ProgressBar {
  display: block;
  position: relative;
  width: 380px;
  height: 6px;
  overflow: hidden;
  border-radius: 3px;
  background: #F5F5F5; }

.ProgressBar-bar {
  position: absolute;
  z-index: 2;
  top: 0;
  bottom: 0;
  left: 0;
  min-width: 1%;
  margin: 0;
  border-radius: 3px;
  background: #1181FB; }

.ProgressBar-bar.is-danger {
  background: #f15743; }

.ProgressBar-bar.is-caution {
  background: #f8b85c; }

.ProgressBar-legend {
  margin-left: 12px;
  color: #999999;
  font-size: 12px;
  font-weight: 300;
  line-height: 12px; }
