.MobileAppsList {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  padding-bottom: 30px; }

.MobileAppsList-item {
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: flex-end;
  width: 100%;
  padding: 20px 0;
  text-decoration: none; }
  .MobileAppsList-item:last-child {
    margin-bottom: 28px; }
  .MobileAppsList-item:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.06); }

.MobileAppsList-itemIcon {
  width: 42px;
  height: 42px;
  margin-right: 20px; }

.MobileAppsList-statusIcon {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%; }

.MobileAppsList-statusIcon--open {
  background-color: #8fb83f; }

.MobileAppsList-statusIcon--dev {
  background-color: #f5a623; }

.MobileAppsList-statusIcon--private {
  background-color: #f15743; }

.MobileAppsList-itemIcon--img {
  border: 0; }

.MobileAppsList-itemInfo {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-between;
  width: 100%; }

.MobileAppsList-itemInfoName {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  justify-content: space-between;
  height: 42px; }

.MobileAppsList-itemInfoTitle {
  max-width: 350px;
  color: #0090d7;
  font-size: 14px;
  font-weight: 500; }

.MobileAppsList-itemInfoSubtitle {
  max-width: 350px;
  color: #999999;
  font-size: 13px;
  font-weight: 300; }

.MobileAppsList-details {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }

.MobileAppsList-details--paragraph {
  color: #979ea1;
  font-size: 14px;
  font-weight: 300; }

.MobileAppsList-detail:not(:last-child) {
  margin-right: 15px; }

.MobileAppsList-details--icon {
  margin-right: 2px;
  font-size: 12px; }
